.main-screen {
  flex: 1 0 100%;
  position: relative;

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0 auto;

    &::before {
      position: absolute;
      content: "";
      width: 150px;
      height: 9px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-image: url("../img/svg/line.svg");
      background-repeat: no-repeat;
      background-position: 0 0;
      z-index: 2;

      @media (max-width: $md4+px) {
        display: none;
      }
    }

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 100%;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      background: radial-gradient(circle, rgba(86, 79, 76, 1) 0%, rgba(86, 83, 76, 0) 100%);
    }
  }

  &__slogan {
    margin-bottom: 135px;
    font-family: "PlayfairDisplay", "Webdings", serif;
    font-size: 92px;
    line-height: 92px;
    font-weight: 700;
    color: $white-color;
    text-align: center;
    position: relative;
    z-index: 1;

    @media (max-width: $md4+px) {
      font-size: 62px;
      line-height: 62px;
      margin-bottom: 20px;
    }

    @media (max-width: $md5+px) {
      font-size: 42px;
      line-height: 42px;
    }
  }

  &__text {
    margin-bottom: 70px;
    max-width: 550px;
    font-size: 24px;
    line-height: 36px;
    font-weight: 400;
    color: $white-color;
    text-align: center;
    position: relative;
    z-index: 1;

    @media (max-width: $md4+px) {
      margin-bottom: 20px;
    }
  }

  &__link {
    width: 200px;
    min-height: 50px;
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    letter-spacing: 4.2px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: $white-color;
    font-size: 14px;
    line-height: 24px;
    background-color: rgba(#7c7872, 0.73);
    border: 1px dashed #7c7872;
    transition: all 0.3s ease 0s;
    position: relative;
    z-index: 1;

    &:hover,
    &:focus {
      background-color: rgba($active-color, 0.83);
      transition: all 0.3s ease 0s;
      outline: none;
    }

    &:active {
      color: rgba($white-color, 0.8);
      background-color: rgba($active-color, 0.43);
      transition: all 0.1s ease 0s;
    }
  }

  &__btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;

    & span {
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 50%);
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: $white-color;
      z-index: 1;
    }

    &::before {
      position: absolute;
      content: "";
      width: 106px;
      height: 106px;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 50%);
      background-image: url("../img/header-icon.png");
      background-position: 0 0;
      background-repeat: no-repeat;
      z-index: 2;
    }
  }
}
