@font-face {
  font-family: "Alegreya";
  font-display: swap;
  src: url("../../fonts/Alegreya-Bold.woff2") format("woff2"), url("../../fonts/Alegreya-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Alegreya";
  font-display: swap;
  src: url("../../fonts/Alegreya-Regular.woff2") format("woff2"), url("../../fonts/Alegreya-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "PlayfairDisplay";
  font-display: swap;
  src: url("../../fonts/PlayfairDisplayBold.woff2") format("woff2"), url("../../fonts/PlayfairDisplayBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "ProximaNova";
  font-display: swap;
  src: url("../../fonts/ProximaNova.woff2") format("woff2"), url("../../fonts/ProximaNova.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Scriptina";
  font-display: swap;
  src: url("../../fonts/Scriptina.woff2") format("woff2"), url("../../fonts/Scriptina.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

/*stylelint-disable*/

html {
  -webkit-text-size-adjust: 100%;
}

hr {
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: inherit;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

small {
  font-size: inherit;
}

sub,
sup {
  font-size: inherit;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

main,
nav,
footer,
header,
aside {
  display: block;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.container {
  max-width: 1290px;
  margin: 0 auto;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

html,
body {
  font-family: "Alegreya", "Webdings", serif;
  color: #56534c;
  height: 100%;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

img {
  vertical-align: top;
  border-style: none;
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}

.section-title {
  color: #56534c;
  font-size: 48px;
  line-height: 48px;
  letter-spacing: 1.2px;
  font-weight: 700;
}

.section-title--white {
  color: #ffffff;
}

.section-subtitle {
  color: #a47d5e;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: 1.4px;
  font-weight: 700;
  text-transform: uppercase;
}

.item-title {
  text-transform: uppercase;
  letter-spacing: 2.5px;
}

.item-title--white {
  color: #ffffff;
}

.section-text {
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
}

.section-text--white {
  color: #ffffff;
}

.section-link {
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: #000000;
  transition: all 0.5s ease 0s;
}

.section-link:hover,
.section-link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
}

.section-link--white {
  color: #ffffff;
}

.section-link--white:hover,
.section-link--white:focus {
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-link--white:active {
  color: rgba(255, 255, 255, 0.3);
  transition: all 0.1s ease 0s;
}

.section-btn {
  width: 155px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  background-color: #000000;
  color: #ffffff;
  transition: all 0.5s ease 0s;
  text-decoration: none;
}

.section-btn:hover,
.section-btn:focus {
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-btn:active {
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.1s ease 0s;
}

.section-btn--white {
  background-color: #ffffff;
  color: #000000;
}

.section-btn--white:hover,
.section-btn--white:focus {
  background-color: rgba(255, 255, 255, 0.8);
}

.section-btn--white:active {
  background-color: rgba(255, 255, 255, 0.6);
}

body.lock {
  overflow: hidden;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.main-screen {
  flex: 1 0 100%;
  position: relative;
}

.main-screen__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.main-screen__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.main-screen__wrap::before {
  position: absolute;
  content: "";
  width: 150px;
  height: 9px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../img/svg/line.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: 2;
}

.main-screen__wrap::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: radial-gradient(circle, #564f4c 0%, rgba(86, 83, 76, 0) 100%);
}

.main-screen__slogan {
  margin-bottom: 135px;
  font-family: "PlayfairDisplay", "Webdings", serif;
  font-size: 92px;
  line-height: 92px;
  font-weight: 700;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 1;
}

.main-screen__text {
  margin-bottom: 70px;
  max-width: 550px;
  font-size: 24px;
  line-height: 36px;
  font-weight: 400;
  color: #ffffff;
  text-align: center;
  position: relative;
  z-index: 1;
}

.main-screen__link {
  width: 200px;
  min-height: 50px;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  letter-spacing: 4.2px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  background-color: rgba(124, 120, 114, 0.73);
  border: 1px dashed #7c7872;
  transition: all 0.3s ease 0s;
  position: relative;
  z-index: 1;
}

.main-screen__link:hover,
.main-screen__link:focus {
  background-color: rgba(164, 125, 94, 0.83);
  transition: all 0.3s ease 0s;
  outline: none;
}

.main-screen__link:active {
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(164, 125, 94, 0.43);
  transition: all 0.1s ease 0s;
}

.main-screen__btn {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.main-screen__btn span {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: #ffffff;
  z-index: 1;
}

.main-screen__btn::before {
  position: absolute;
  content: "";
  width: 106px;
  height: 106px;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
  background-image: url("../img/header-icon.png");
  background-position: 0 0;
  background-repeat: no-repeat;
  z-index: 2;
}

.main-header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  right: 0;
  min-height: 70px;
  padding-top: 18px;
  padding-bottom: 8px;
}

.main-header::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 70px;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: -1;
}

.main-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__burger {
  display: none;
}

.main-header__logo {
  transform: translateY(-5px);
}

.main-header__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.main-header__social-list li {
  margin-left: 14px;
  margin-right: 14px;
}

.main-header__social-list li:first-child {
  margin-left: 0;
}

.main-header__social-list li:last-child {
  margin-right: 0;
}

.main-header__social-item svg {
  transition: all 0.3s ease 0s;
}

.main-header__social-item svg:hover,
.main-header__social-item svg:focus {
  fill: #a47d5e;
  transition: all 0.3s ease 0s;
  outline: none;
}

.main-header__social-item svg:active {
  fill: rgba(164, 125, 94, 0.55);
  transition: all 0.1s ease 0s;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: flex-start;
}

.navigation__list li {
  margin-left: 43px;
  margin-right: 43px;
  position: relative;
}

.navigation__list li::after {
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 6px;
  height: 2px;
  background-image: url("../img/nav-item.png");
  background-position: 0 0;
  background-repeat: no-repeat;
  transform: translate(40px, -50%);
}

.navigation__list li:first-child {
  margin-left: 0;
}

.navigation__list li:last-child {
  margin-right: 0;
}

.navigation__list li:last-child::after {
  display: none;
}

.navigation__link {
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  letter-spacing: 4.2px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
}

.navigation__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  bottom: 0;
  left: 0;
  transform: translateY(4px) scale(0);
}

.navigation__link:hover,
.navigation__link:focus {
  transition: all 0.3s ease 0s;
  outline: none;
}

.navigation__link:active {
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.1s ease 0s;
}

.logo span {
  font-family: "Scriptina", "Webdings", serif;
  font-size: 30px;
  font-weight: 400;
  line-height: 30px;
  color: #ffffff;
}

.cakes {
  padding-top: 105px;
  padding-bottom: 105px;
}

.cakes__row {
  display: flex;
}

.cakes__col {
  flex: 0 1 50%;
}

.cakes__col:first-child {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cakes__subtitle {
  margin-bottom: 60px;
}

.cakes__text {
  max-width: 490px;
  font-family: "ProximaNova", "Webdings", serif;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 1.4px;
  font-weight: 400;
  margin-bottom: 40px;
}

.cakes__review {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cakes__review-title {
  margin-bottom: 45px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  letter-spacing: 0.6px;
}

.main-footer {
  padding-top: 34px;
  padding-bottom: 27px;
}

.main-footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-footer__text {
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: #4c4c4c;
}

.main-footer__text a {
  color: #4c4c4c;
}

@media (min-width: 879.98px) {
  .navigation__link:hover::after,
  .navigation__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.3s ease 0s;
  }

  .navigation__link:active::after {
    background-color: rgba(255, 255, 255, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }
}

@media (max-width: 1029.98px) {
  .navigation__list li::after {
    transform: translate(20px, -50%);
  }

  .navigation__list li {
    margin-left: 23px;
    margin-right: 23px;
  }
}

@media (max-width: 879.98px) {
  .navigation__list li::after {
    display: none;
  }

  .navigation__list li {
    margin-left: 13px;
    margin-right: 13px;
  }

  .main-footer__row {
    flex-direction: column;
  }

  .main-footer__col:first-child {
    order: 2;
  }

  .main-footer__col:last-child {
    order: 1;
    margin-bottom: 10px;
  }

  .main-footer__text {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 789.98px) {
  .main-screen__wrap::before {
    display: none;
  }

  .main-screen__slogan {
    font-size: 62px;
    line-height: 62px;
    margin-bottom: 20px;
  }

  .main-screen__text {
    margin-bottom: 20px;
  }

  .main-header::before {
    min-height: 160px;
  }

  .main-header .container {
    flex-direction: column;
  }

  .main-header__logo {
    transform: translateY(0);
    margin-bottom: 20px;
  }

  .main-header__social-list {
    justify-content: center;
  }

  .navigation {
    margin-bottom: 20px;
  }
}

@media (max-width: 589.98px) {
  .main-screen__slogan {
    font-size: 42px;
    line-height: 42px;
  }

  .main-header::before {
    min-height: 70px;
    background-color: rgba(164, 125, 94, 0.97);
  }

  .main-header.active::before {
    background-color: rgba(164, 125, 94, 0.97);
    z-index: 4;
  }

  .main-header__burger {
    display: block;
    width: 30px;
    height: 20px;
    z-index: 4;
    background-color: transparent;
    position: absolute;
    top: 24px;
    left: 24px;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .main-header__burger span {
    background-color: #ffffff;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger span:hover,
  .main-header__burger span:focus {
    outline: none;
  }

  .main-header__burger::before,
  .main-header__burger::after {
    content: "";
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before {
    top: 0;
  }

  .main-header__burger::after {
    bottom: 0;
  }

  .main-header__burger.active span {
    transform: scale(0);
  }

  .main-header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #ffffff;
  }

  .main-header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #ffffff;
  }

  .main-header__burger:hover,
  .main-header__burger:focus {
    outline: none;
  }

  .main-header__logo {
    position: absolute;
    left: 50%;
    top: 12px;
    transform: translateX(-50%);
    z-index: 5;
    margin-bottom: 0;
  }

  .main-header__social-item svg {
    fill: rgba(255, 255, 255, 0.65);
  }

  .main-header__social-item svg:hover,
  .main-header__social-item svg:focus {
    fill: #ffffff;
    transition: all 0.3s ease 0s;
    outline: none;
  }

  .main-header__social-item svg:active {
    fill: rgba(255, 255, 255, 0.55);
    transition: all 0.1s ease 0s;
  }

  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all 0.6s ease 0s;
    padding: 100px 10px 20px 10px;
    z-index: 2;
  }

  .navigation.active {
    top: 0;
    background-color: rgba(0, 0, 0, 0.6);
  }

  .navigation--da .main-header__social-list {
    margin-top: 14px;
  }

  .navigation__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navigation__list li {
    margin-bottom: 16px;
  }

  .navigation__link {
    position: relative;
    font-size: 25px;
    font-weight: 700;
    line-height: 1.4;
    color: rgba(255, 255, 255, 0.75);
  }

  .navigation__link:hover,
  .navigation__link:focus {
    color: white;
  }

  .navigation__link:active {
    color: rgba(255, 255, 255, 0.55);
    transition: all 0.1s ease 0s;
  }
}